import $axios from "@/common/http"
import axios from "axios"
import router from "@/router"
import store from "@/store"

import { $Message } from "@/common/utils"

// 


// 
export async function getGoodsList(page = 1, size = 10) {
    let res = await $axios.get('/goodslist', {
        params: {
            size,
            page
        }
    });
    if (res.list) {
        return res.list
    } else {
        return [];
    }
}

export async function getGoodsArr(fid,sid) {
    let res = await $axios.get('/goodslist', {
        params:{
            fid,
            sid
        }
    });
    if (res.list) {
        return res.list
    } else {
        return [];
    }
}


/**
 * 
 * @param {*} id
 */
export async function getGoodsInfo(id) {
    let res = await $axios.get('/goodsinfo?id=' + id);
    if (res.list) {
        return res.list
    } else {
        return [];
    }
}

/**
 * 
 */
export async function getTotal() {
    let res = await $axios.get('/goodscount');
    if (res.list) {
        return res.list[0].total;
    } else {
        return 0;
    }
}



/**
 * 
 * @param {*} data   
 */
export async function addGoods(data) {
    let res = await $axios.post('/goodsadd', data);
    if (res.code == 200) {
        $Message('success', '', () => {
            router.back();
            store.dispatch("category/getlist")
        })
    }
}



/**
 * 
 * @param {*} id   id
 * @param {*} data    FormData
 */
export async function editGoods(id, data) {
    data.append('id', id)
    let res = await $axios.post('/goodsedit', data);
    if (res.code == 200) {
        $Message('success', '', () => {
            router.back();
            store.dispatch("category/getlist")
        })
    }
}


/**
 * 
 * @param {*} id id
 */
export async function delGoods(id) {
    let res = await $axios.post('/goodsdelete', { id });
    if (res.code == 200) {
        $Message('success', '')
    }
}


